.login-page {
  background-color: #eeeeee;
}

.register-page {
  background-color: #eeeeee;
}

.form {
    margin: auto;
    width: 400px;
    padding: 30px;
  }
  
  .input-container {
    border: 2px solid #ccc;
    border-radius: 10px;
    padding: 10px;
  }
  
  .form h1 {
    font-size: 36px;
    text-align: center;
  }
  
  .form .input-box {
    position: relative;
    width: 100%;
    height: 50px;
    margin: 20px 0;
    display: flex;
  }
  
  .input-box select {
    width: 100%;
    height: 100%;
    background: #fff;
    border: none;
    outline: none;
    border: 2px solid rgba(255, 255, 255, 0.2);
    text-align-last: center;
    border-radius: 40px;
    padding-right: 30px;
  }
  
  .input-box input {
    width: 100%;
    height: 100%;
    background: transparent;
    border: none;
    outline: none;
    border: 2px solid rgba(255, 255, 255, 0.2);
    border-radius: 40px;
    padding-right: 30px;
  }
  
  .input-box .icon {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    font-size: 20px;
  }
  
  .form .forgot {
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    margin: -15px 0 15px;
    margin-top: 10px;
  }
  
  .forgot label {
    display: flex;
    align-items: center;
  }
  
  .forgot label input[type="checkbox"] {
    margin-right: 5px;
  }
  
  .forgot label input {
    margin-right: 4px;
  }
  
  .forgot a {
    text-decoration: none;
  }
  
  .forgot a:hover {
    text-decoration: underline;
  }
  
  .form .button {
    margin-top: 10px;
    width: 100%;
    height: 45px;
    background: #881c1c;
    border: none;
    outline: none;
    border-radius: 40px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    font-size: 16px;
    color: #fff;
    font-weight: 700;
  }
  
  .form .register {
    font-size: 14px;
    text-align: center;
    margin-top: 20px;
  }
  
  .register a {
    text-decoration: none;
    font-weight: 600;
  }
  
  .register a:hover {
    text-decoration: underline;
  }
  
